        </div>
        <ModalSharePopup :idFile="idFile" @StorageDocumentListing="StorageDocumentListing" :folderId="folderId" :docCode="docCode" :StorageUniqueCode="StorageUniqueCode"
            :generateJSONData=generateJSONData :Name="Name" v-if="sharemodal == true"></ModalSharePopup>
        <ModelGroupListing :docCode="docCode" :folderId="folderId" @ModalGroupListing="ModalGroupListing"
            :StorageUniqueCode="StorageUniqueCode" 
            @CloseAddGroupPopup="CloseAddGroupPopup" @CloseGroupListingPopup="CloseGroupListingPopup"
            :ModalLabelForlsListing="ModalLabelForlsListing" :FileId='FileId' :generateJSONData="generateJSONData"
            :refCode="refCode" v-if="ShowGruopListing">
        </ModelGroupListing>
        <ModalMoveFolderFiles :FolderArray="FindalFolderArray" :additionalData="additionalOrganizationSpace" :defaultData="organizationSpace" :mySpaceOrOrganization="mySpaceOrOrganization" :organizationSpace="AddtionalStorageFileId != '' ? additionalOrganizationSpace : organizationSpace" @MoveFoldersFiles="MoveFoldersFiles"
            @CloneFoldersFiles="CloneFoldersFiles" :checkActionType="checkActionType" :checkType="checkType"
            @closemovefilefolderpopup="closemovefilefolderpopup" @CopyFileFolder="CopyFileFolder"
            @FolderIdmove="FolderIdmove" v-if="showmovefolderpopup == true"></ModalMoveFolderFiles>
        <FilesListOldVersion :filesOldVersionListing="filesOldVersionListing" v-if="showFilesOldVersion == true"
            @closeOldVersionPopUp="closeOldVersionPopUp"></FilesListOldVersion>
        <ModalAddGroup @ModalAddGroups="ModalAddGroups" @CloseAddGroupPopup="CloseAddGroupPopup" v-if="ShowModalAddGroup">
        </ModalAddGroup>
        <copy-confirmation v-if="copyFileInfo" :copyFileInfo="copyFileInfo"
            @SaveCopyFile="SaveCopyFile"></copy-confirmation>
    </div>
</template>
<style>
.img-ico i.fa-file-pdf-o {
    font-size: 40px;
    color: #c12025;
}
