<script>

import axios from "axios";
import LeftMenu from "@/components/LeftMenu.vue"
import DataService from "../../services/DataService";
import doctimeline from "../../components/Common/Modal/DocTimeLine.vue";
import ModalSharedDetails from "../../components/Common/Modal/ModalSharedDetails.vue";
import ModelGroupListing from "./Modal/ModalGruopListing.vue"
import ModalSharePopup from "@/views/UI/ShareDocument.vue"
// import SharedUserList from "./Modal/ModelSharedMembers.vue"
import ModalAddGroup from "./Modal/ModalAddGroup.vue"
import ModalMoveFolderFiles from "./Modal/ModalMoveFolderFiles.vue"
import FilesListOldVersion from "./Modal/FileOldVersion.vue"
import VueEasyLightbox from 'vue-easy-lightbox'
//import 'vue-easy-lightbox/external-css/vue-easy-lightbox.css'
import CopyConfirmation from './Modal/CopyConfirmation.vue';
const docstorage = process.env.VITE_DOCSTORAGE_API;
export default {
    components: {
        LeftMenu,
        doctimeline,
        ModalSharedDetails,
        ModelGroupListing,
        ModalMoveFolderFiles,
        ModalSharePopup,
        ModalAddGroup,
        VueEasyLightbox,
        FilesListOldVersion,
        CopyConfirmation
        // SharedUserList
    },
    data() {
        return {
            isLoading: false,
            loggeduserid: null,
            SharedFolderParent: [],
            FindalFolderArray: [],
            folderTreeList: [],
            sharedFolder: [],
            abc: [],
            FileJsonData: [],
            SharedType: "",
            images: '',
            CreatedOn: "",
            ishow: false,
            isShared: "",
            FileName: "",
            visibleRef: false,
            ShowGruopListing: false,
            showmovefolderpopup: false,
            folderFileId: null,
            ModalLabelForlsListing: '',
            refCode: '',
            FileId: '',
            docCode: null,
            fileTimelineData: [],
            sharemodal: false,
            ShowModalAddGroup: false,
            url: "",
            fileType: "",
            showFilesOldVersion: false,
            filesOldVersionListing: [],
            files: '',
            folderId: '',
            StorageUniqueCode: '',
            recentFolderList: [],
            pinnedFolderList: [],
            RecentFolderArray: [],
            pinnedFolderArray: [],
            organizationSpace: [],
            tool: false,
            text: "Copied!",
            permission: null,
            checkActionType: '',
            fileOldVersionCount: null,
            isGenerateLink: null,
            uploadUrl: process.env.VUE_APP_DOCSTORAGE_API + '/UploadStream',
            token: null,
            extension: null,
            generateJSONData: [],
            iconClass: "",
            sharedparentfolder: [],
            sharedChildFolder: [],
            commonParentName: "",
            refCode: '',
            addClass: '',
            copyFileInfo: null,
            imgpath: null,
            copylinkUrl: '',
            thumbnail: null,
            IsGlobal: 0,
            videoPlayerShow: false,
            SharedUserList:[],
            userType:null,
            mySpaceOrOrganization:null,
            loggedUserName:null,
            createdBy:null,
            IsAllowDownload:'yes',
            additionalOrganizationSpace:[],
            fileSharedDepartmentCount:0,
            fileGroupSharedDocument:0,
            projectFileSharedCount:0,
            fileUserSharedCount:0,
            AdditionalFolderIdExist:undefined,
            AddtionalStorageFileId:"",
            checkType:'',
            AdditionalStorageId:0,
            CopyAdditionalStorageId:0,
            checkMoveType:''
        };
    },
    created: function () {
        var pathname = window.location.pathname;
        this.loggeduserid = this.GetUserInfo.ID;
        this.loggedUserName=this.GetUserInfo.Name;
        this.userType = this.GetUserInfo.UserType;
        this.token = this.GetUserInfo.Token;
        this.docCode = this.$route.params.docCode;
        this.permission = this.$route.params.Permission;
        this.extension = this.$route.params.fileType;
        this.refCode = this.$route.params.RefCode;
        this.addClass = this.$route.params.RouteClass;
        this.FileId = pathname.split("/")[2];
        this.GetFullTree();
        //this.GetOrganizationSpace();
        if (pathname.split("/")[2], pathname.split("/")[3]) {
            this.GetStorageFileSummary(pathname.split("/")[2]);
        };
        this.images = this.$route.params.Path
        this.folderId = pathname.split("/")[4]
        this.commonParentName = this.$route.params.CommonParentName
        this.GetClass(this.extension);
        this.GetSharedFolder();
        this.GetConfigType();
    },
    mounted() {
        this.ShowPopOver();
    },
    updated: function () {
        var vm = this;
        vm.FileId = vm.$route.params.FileId;
        setTimeout(function () {
            vm.setHeaderForSelectedTab();
        }, 500);
        $("#ancActivityExpandAllCollapseAll").click(function () {
            if ($(this).attr("show") == "0") {
                $(this).attr("show", "1");
                $("#divconversationbox")
                    .find("#accordion")
                    .find(".collapse")
                    .collapse("hide");
            } else {
                $(this).attr("show", "0");
                $("#divconversationbox")
                    .find("#accordion")
                    .find(".collapse")
                    .collapse("show");
            }
        });
    },

    mounted: function () {
        $("#FolderFullTree").treeview();
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $("#FolderFullTree .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
        $("#FolderFullTree3").treeview();
        $("body").on("click", "#FolderFullTree3 .folderlink", function () {
            $("#FolderFullTree3 .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
        //this.GetStorageFileSummary();
    },

    methods: {
        DownloadFiles: function (fileId, fileName) {
            var vm = this;
            var IsVersion = 0
            vm.DownloadFile(fileId, fileName, IsVersion)
            vm.GetStorageFileSummary(fileId);
        },
        //Download files method
        DownloadFile: function (fileId, fileName, IsVersion) {
            debugger
            var vm = this;
            vm.isLoading = true;
            var params = `fileId=${fileId}&IsVersion=${IsVersion}`
            if(vm.AddtionalStorageFileId!=null && vm.AddtionalStorageFileId!=""){
                var param=`fileId=${vm.AddtionalStorageFileId}&additionalStorageId=${vm.AdditionalStorageId}`
                DataService.DownloadAdditionalStorageFile(param).then((response) => {
                    var link = document.createElement('a');
                    link.href = response.data;

                    // Set the download attribute to force download
                    link.download = fileName;

                    // Append the anchor element to the document body
                    document.body.appendChild(link);

                    // Trigger a click event to initiate the download
                    link.click();
                })
            }else{
                DataService.DownloadFile(params).then((response) => {
                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName.toString());
                    document.body.appendChild(fileLink);
                    fileLink.click();
                    vm.isLoading = false;
                })
            }
        },
        StorageDocumentListing:function(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId){
            var vm=this;
            vm.$emit('StorageDocumentListing',folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId)
        },
        //open popup for share document
        SharePopup: function () {
            var vm = this;
            vm.generateJSONData = [];
            vm.sharemodal = true;
            vm.idFile = vm.FileJsonData[0].storage_file_id;
            vm.docCode = vm.$route.params.docCode;
            vm.StorageUniqueCode = vm.FileJsonData[0].StorageUniqueCode;
            var obj = {
                Name: vm.FileJsonData[0].storage_file_name
