        convertmbtoKb: function (valInMB) {
            var convertValue;
            var convertValue;
            if (valInMB >= 1) {
                convertValue = valInMB.toFixed(2) + ' MB';
            } else {
                var valInKB = valInMB * 1024;
                if (valInKB < 1) {
                    convertValue = valInKB.toFixed(2) + ' Bytes';
                } else {
                    convertValue = valInKB.toFixed(2) + ' KB';
                }
            }
            return convertValue;
        },
