        submitFiles() {
            let vm = this;
            vm.isLoading = true;
            let formData = new FormData();

            /*
              Iteate over any file sent over appending the files
              to the form data.
            */
            vm.folderId == null ? 0 : vm.folderId;
            formData.append("fileId", vm.FileId);
            formData.append("folderId", vm.folderId);
            formData.append("moduleName", 'DocStorage');
            formData.append("subModuleName", '');
            formData.append("isGlobal", vm.IsGlobal);
            formData.append("file", this.files);
            axios
                .post(this.uploadUrl, formData, {
                    headers: {
                        'Authorization': 'Bearer ' + this.token
                    },
                })
                .then((response) => {

                    if (response.data == 'Success') {
                        vm.ShowAlert("File new version has been upload successfully", "success", true);
                        vm.isLoading = false;
                        vm.GetStorageFileSummary(vm.FileId);
                    }
                    console.info(response.data);
                })
                .catch((err) => {
                    console.error(err);
                    vm.isLoading = false;
                });

        },

