        GetClass: function (cl) {
            var vm = this;
            if (cl == "pdf" || cl == "xlsx" || cl == "docx" || cl == "jpg" || cl == "jpeg" || cl == "png") {
                vm.iconClass = vm.thumbnail
            }
            else if (cl == "txt") {
                vm.iconClass = "fa fa-file-text-o mt-4"
            }
            else if (cl == "xls") {
                vm.iconClass = "fa fa-file-excel-o mt-4"
            }
            else if (cl == "mp3") {
                vm.iconClass = "fa fa-file-audio-o mt-4"
            }
            else if (cl == "mp4") {
                vm.iconClass = "fa fa-file-video-o mt-4"
            }
            else {
                vm.iconClass = "fa fa-file-o mt-4"
            }
            return vm.iconClass;
