<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            {{ $t('OldVersion') }}
                        </h4>
                        <button @click="ClosePopupOldVersion" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                <thead class="thead-bg">
                                    <tr>
                                       
                                        <th>
                                            <span>{{ $t('FileName') }}</span>
                                        </th>
                                        <th>
                                            <span>Version</span>
                                        </th>
                                        <th>
                                            <span>Uploaded By</span>
                                        </th>
                                        <th data-width="5%" class="text-center nonresize">
                                            <span>Action</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in filesOldVersionListing" :key="item">
                                      

                                        <td>
                                            <span class="text-center">
                                                {{ item.FILE_NAME }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-center">
                                                {{ item.FILE_VERSION }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-center">
                                                {{ item.POSTED_BY }}
                                            </span>
                                        </td>
                                        <td class="text-center single-action">
                                            <em class="fa fa-download text-info action-icon" aria-hidden="true" style="cursor:pointer;" @click="DownloadFiles(item.FILE_VERSION_ID,item.FILE_NAME)"></em>  
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="filesOldVersionListing.length == 0" colspan="7"
                                            class="text-danger text-center no-record">{{$t("NoRecordFound")}}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>
import ListMenu  from "../../../components/LeftMenu.vue"
import DataService from "../../../services/DataService";
export default {
    components: {
        ListMenu
    },
    props: {
        filesOldVersionListing: {
            type: Array,
            required: true
        }
    },
    data(){
        return{
            fileId: '', 
            fileName: '',
            
        }
    },
    methods: {
        ClosePopupOldVersion: function () {
            var vm = this;
            vm.$emit("closeOldVersionPopUp")
        },
        DownloadFiles: function(fileId, fileName)
        {
            
            var vm=this;
            var IsVersion=1;
            vm.DownloadFile(fileId, fileName,IsVersion)
        },
        DownloadFile: function (fileId, fileName,IsVersion) {
            var vm = this;
            var params = `fileId=${fileId}&IsVersion=${IsVersion}`
            DataService.DownloadFile(params).then((response) => {
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', fileName.toString());
                document.body.appendChild(fileLink);
                fileLink.click();

            })

        },
    }
}
</script>